footer {
  box-sizing: border-box;
  height: 100vh;
  background-image: url('./photos/footermob.png');
  background-size: cover;
  background-position: right;
  z-index: -1;

  @include on-tablet {
    background-image: url('./photos/footertablet.png');
  }

  @include on-desktop {
    background-image: url('./photos/footerdesctop.png');
  }
}
